import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Play, Eye, AlertTriangle, CheckCircle } from "lucide-react";
import { query } from "@/lib/db"; // ✅ Fixed import

// Force dynamic to ensure fresh data on load
export const dynamic = "force-dynamic";

export default async function WebsitesPage() {
  // ✅ Use your existing Supabase query helper
  const { data: websites } = await query("websites");

  // Safe fallback if data is null
  const safeWebsites = websites || [];

  return (
    <div className="space-y-6 p-8">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">
            Website Generation
          </h1>
          <p className="text-muted-foreground">
            Manage AI-generated sites and QA reports.
          </p>
        </div>
        <Button>
          <Play className="mr-2 h-4 w-4" />
          Bulk Generate (Beta)
        </Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {safeWebsites.map((site: any) => (
          <Card key={site.id} className="border shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-lg font-medium">
                {site.subdomain || "Untitled"}
              </CardTitle>
              <StatusBadge status={site.status} />
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="text-sm text-muted-foreground">
                  Template:{" "}
                  <span className="font-medium text-foreground">
                    {site.template_id}
                  </span>
                </div>

                {site.status !== "draft" && (
                  <div className="flex items-center gap-2 text-sm">
                    <span>QA Score:</span>
                    <span
                      className={`font-bold ${
                        site.qa_score > 80 ? "text-green-600" : "text-red-600"
                      }`}
                    >
                      {site.qa_score}/100
                    </span>
                  </div>
                )}

                <div className="flex gap-2 pt-2">
                  <Button variant="outline" size="sm" className="w-full">
                    <Eye className="mr-2 h-4 w-4" /> Preview
                  </Button>
                  {site.status === "flagged" && (
                    <Button variant="destructive" size="sm" className="w-full">
                      Fix Issues
                    </Button>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        ))}

        {safeWebsites.length === 0 && (
          <div className="col-span-full p-12 text-center border-2 border-dashed rounded-xl">
            <p className="text-muted-foreground">No websites generated yet.</p>
          </div>
        )}
      </div>
    </div>
  );
}

function StatusBadge({ status }: { status: string }) {
  if (status === "generated")
    return (
      <Badge className="bg-green-50 text-green-700 hover:bg-green-100">
        <CheckCircle className="w-3 h-3 mr-1" /> Ready
      </Badge>
    );
  if (status === "flagged")
    return (
      <Badge variant="destructive">
        <AlertTriangle className="w-3 h-3 mr-1" /> QA Failed
      </Badge>
    );
  return <Badge variant="secondary">Processing</Badge>;
}
